<template>
	<div>
		<v-container>
			<v-row>
				<v-col cols="10" class="font-weight-bold subtitle-2 py-0 mt-5">
					 
				</v-col>
				<v-col cols="1" class="pl-2 pt-0 mt-4">
					<v-menu transition="slide-y-transition" bottom :offset-y="true">
						<template v-slot:activator="{ on, attrs }">
							<v-btn
								v-if="closeBtnShow"
								icon
								small
								@click="closeBtnShow = false"
							>
								<v-icon color="success">mdi-check-underline </v-icon>
							</v-btn>
							<v-btn v-else icon v-bind="attrs" v-on="on" small>
								<!-- <v-icon>mdi-cog-outline </v-icon> -->
								<!-- <v-icon>mdi-tune-variant </v-icon> -->
								<v-icon>mdi-tune</v-icon>
							</v-btn>
						</template>
						<v-list>
							<v-list-item v-for="(item, i) in schTermSettings" :key="i">
								<v-list-item-title @click="action(item.action)">{{
									item.title
								}}</v-list-item-title>
							</v-list-item>
						</v-list>
					</v-menu>

					<!-- <v-icon>mdi-dots-vertical</v-icon> -->
				</v-col>
				<v-col cols="1" class="px-0" />
			</v-row>
			<v-row justify="space-around">
				<v-col cols="12" class="pt-0">
					<v-sheet elevation="0" class="pt-0 pb-2">
						<v-chip-group>
							<v-chip
								v-for="schTerm in rcntSrchTerms"
								:key="schTerm.id"
								close-icon="mdi-close"
								:close="closeBtnShow"
							>
								{{ schTerm.word }}
							</v-chip>
						</v-chip-group>
					</v-sheet>
				</v-col>
			</v-row>
		</v-container>
		<v-container>
			<v-row>
				<v-col cols="11" class="font-weight-bold subtitle-2 py-0 mt-5">
					 
				</v-col>
			</v-row>
			<v-row justify="space-around">
				<v-col cols="12">
					<v-sheet elevation="0" class="pt-1 pb-2">
						<v-chip-group column active-class="primary--text">
							<v-chip v-for="schTerm in rcmmSrchTerms" :key="schTerm.id">
								{{ schTerm.word }}
							</v-chip>
						</v-chip-group>
					</v-sheet>
				</v-col>
			</v-row>
		</v-container>
	</div>
</template>

<script>
	export default {
		data: () => ({
			closeBtnShow: false,
			rcntSrchTerms: [
				{ id: 1, word: '120g' },
				{ id: 2, word: '' },
				{ id: 3, word: 'vue' },
				{ id: 4, word: 'vuetify' },
				{ id: 5, word: '' },
				{ id: 6, word: '' },
				{ id: 7, word: '' },
			],
			rcmmSrchTerms: [
				{ id: 1, word: '' },
				{ id: 2, word: '' },
				{ id: 3, word: '' },
				{ id: 4, word: '' },
				{ id: 5, word: '' },
				{ id: 6, word: '' },
				{ id: 7, word: '' },
				{ id: 8, word: ' ' },
				{ id: 9, word: '' },
				{ id: 10, word: '' },
				{ id: 11, word: '' },
			],
			schTermSettings: [
				{
					title: ' ',
					action: 'select',
					// action: ()=> {
					//   this.closeBtnShow = true;
					// },
				},
				{ title: ' ', action: 'all' },
				{ title: ' ', action: 'auto' },
			],
		}),
		methods: {
			action(type) {
				switch (type) {
					case 'select':
						this.closeBtnShow = true;
						break;
					case 'all':
						alert('!');
						break;
					case 'auto':
						alert(' ?');
						break;
					default:
						console.log('  .');
						break;
				}
			},
		},
	};
</script>

<style></style>
